<template>
	<div>
		<b-navbar toggleable="md" type="dark" variant="primary">
		  <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
		  <b-navbar-brand href="#">Albemarle IoT Devices</b-navbar-brand>
		  <b-collapse is-nav id="nav_collapse">
			<b-navbar-nav>
			  <b-nav-item-dropdown text="Export Options">
				<b-dropdown-item href="#">Excel</b-dropdown-item>
				<b-dropdown-item href="#">CSV</b-dropdown-item>
				<b-dropdown-item href="#">JSON</b-dropdown-item>
			  </b-nav-item-dropdown>
			  <b-nav-item href="#" @click="showAboutModal()">Disclaimer and About</b-nav-item>
			</b-navbar-nav>
		  </b-collapse>
		</b-navbar>
		<br></br>
		<b-table striped hover fixed stacked="md" bordered fixed :items="results" :fields="fields"></b-table>
	</div>
</template>

<script>

import axios from 'axios'

export default {
  data () {
    return {
	  results: [],
	  fields: [{key:'ip', label:'IP Address', sortable: true}, {key:'org', label:'Organization', sortable: true}, {key:'devicetype', label:'Device Type', sortable: true}, {key:'product', label:'Product', sortable: true}]
    }
  },
  methods: {
	axiosGetResults: function() {
		axios.get('/results.json')
		.then(response => {
			this.results = JSON.parse(response.data)
		})
	},
	showAboutModal: function() {
		alert("Programmed By Soulstice. Using NodeJS to parse the API, and VueJS to serve it")
	}
  },
  created () {
	  this.axiosGetResults()
	}
}
</script>


